function sliderBrands() {
    var swiper = new Swiper('.brands .swiper', {
        slidesPerView: '5',
        spaceBetween: 140,
        navigation: {
            nextEl: ".brands .swiper-button-next",
            prevEl: ".brands .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            360: {
                slidesPerView: 2,
            },
            575: {
                slidesPerView: 2,
            },
            767: {
                slidesPerView: 3,
            },
            991: {
                slidesPerView: '4',
            },
            1200: {
                slidesPerView: '5',
            },
        }
    })
}

let SlidersArray = []

function sliderServices() {
    $(".services").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.services[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 3,
            spaceBetween: 20,
            navigation: {
                nextEl: `.services[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.services[data-slider-id="${index}"] .swiper-button-prev`,
            },
            breakpoints: {
                320: {
                    slidesPerView: 1,
                    centeredSlides: false,
                    loop: false,
                },
                401: {
                    slidesPerView: 1.5,
                    centeredSlides: 1,
                    loop: true,
                },
                577: {
                    slidesPerView: 2,
                },
                767: {
                    slidesPerView: 3,
                },
            }
        })
    })
}

function sliderReviews() {
    var swiper = new Swiper('.reviews .swiper', {
        slidesPerView: 3,
        spaceBetween: 50,
        navigation: {
            nextEl: ".reviews .swiper-button-next",
            prevEl: ".reviews .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 20,
            },
            576: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            992: {
                slidesPerView: 3,
                spaceBetween: 50,
            },
        }
    })
}

function sliderNew() {
    var swiper = new Swiper('.new .swiper', {
        slidesPerView: 4,
        spaceBetween: 30,
        navigation: {
            nextEl: ".new .swiper-button-next",
            prevEl: ".new .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 20,
                centeredSlides: false,
                loop: false,
            },
            401: {
                slidesPerView: 1.5,
                spaceBetween: 20,
                centeredSlides: 1,
                loop: true,
            },
            575: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            767: {
                slidesPerView: 3,
                spaceBetween: 30,
            },
            991: {
                slidesPerView: 4,
                spaceBetween: 30,
            },
        }
    })
}


$(document).ready(function() {

    sliderBrands();
    sliderServices();
    sliderReviews();
    sliderNew();

    // Like
    $('.services-card__like').click(function () {
        $(this).toggleClass('_active')
    })

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()


})